<template>
	<div id="auth-login">
		<public-header title="登录"></public-header>
		<div class="content">
			<div class="plateform-logo">
				<img class="wh-100" :src="config.logo" alt="">
			</div>
			<div class="plateform-title">{{config.title}}</div>
			<div class="input-group">
				<div class="input">
					<input v-model.number="mobile" placeholder="请输入手机号" type="text">
				</div>
				<div class="input">
					<input v-model="password" type="password" placeholder="请输入密码">
				</div>
			</div>
			<div class="operation">
				<router-link tag="div" :to="util.getUrl({path: '/pages/auth/forget'})" class="flex font-14">
					忘记密码
					<div class="icon icon-question1 margin-5-l"></div>
				</router-link>
			</div>
			<div class="save-btn">
				<van-button size="normal" block class="bg-info" :disabled="!islegal" @click="onSubmit">登录</van-button>
			</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon.js"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				config: {
					title: '',
					logo: '',
				},
				mobile: '',
				password: '',
				showPreLoading: true,
				islegal: false
			}
		},
		methods: {
			onLoad() {
				var that = this;
				var clerkInfo = that.util.getStorage('clerkInfo');
				if(clerkInfo && clerkInfo.token) {
					alert(10);
					that.$router.replace(that.util.getUrl({path: '/pages/order/index'}));
					return;
				}
				getData({
					vue: that,
					url: 'manage/auth/login',
					data: {
						nosid: 1
					},
					autoAssign: true,
					variable: 'config',
					success: () => {
						that.islegal = true;
					}
				})
			},
			onSubmit() {
				var that = this;
				if(!that.mobile) {
					that.util.$toast('请输入手机号');
					return;
				}
				if(!that.password) {
					that.util.$toast('请输入密码');
					return;
				}
				setData({
					vue: that,
					url: 'manage/auth/login',
					data: {
						mobile: that.mobile,
						password: that.password,
						nosid: 1,
					},
					success: function(res) {
						that.util.setStorage('clerkInfo', {token: res.clerk.token});
						let sids = res.sids;
						alert(11);
						if(sids.length == 1) {
							alert(12);
							that.util.setStorage('__sid', sids[0]);
							var forwardUrl = that.util.getStorage('forwardUrl');
							if(forwardUrl) {
								alert(13);
								that.util.$toast('登录成功', '', 1000);
								window.location.href = forwardUrl;
							} else {
								alert(14);
								that.util.$toast('登录成功', that.util.getUrl({path: '/pages/order/index'}), 1000, 'replace');
							}
							return;
						} else if(sids.length > 1) {
							alert(15);
							that.util.$toast('登录成功', that.util.getUrl({path: '/pages/shop/select'}), 1000, 'replace');
							return;
						}
					},
					fail: function(res) {
						that.util.$toast(res.message);
						that.islegal = true;
					},
				})
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#auth-login .content{
		background-color: #fff;
	}
	#auth-login .plateform-logo{
		width:100px;
		height:100px;
		border-radius:50%;
		overflow:hidden;
		margin:40px auto 0px;
		border:1px solid #eee;
	}
	#auth-login .plateform-title{
		text-align:center;
		font-size:15px;
		margin: 20px 0;
		color:#33aafc;
	}
	#auth-login .input-group{
		width:100%;
		padding:0 40px;
		box-sizing:border-box;
	}
	#auth-login .input-group .input{
		display:flex;
		flex-flow:row nowrap;
		align-items:center;
		justify-content:space-between;
		border-bottom:1px solid #dadada;
		padding:10px 0px;
		margin-bottom:20px;
	}
	#auth-login .input-group .input input{
		width: 100%;
		background-color: #fff!important;
		font-size: 15px;
	}
	#auth-login .operation{
		display:flex;
		flex-flow:row nowrap;
		align-items:center;
		justify-content:space-between;
		padding:0px 40px;
		color:#999;
	}
	#auth-login .save-btn{
		padding:0 40px;
		margin-top:40px;
	}
</style>


